
import { Vue, Prop, Component } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
import { recItems } from "../utils/RecommendedItems";
import { CartItem } from "../types";

const categoryModule = namespace('CategoryModule');
const modal = namespace('Modal');
const cart = namespace('Cart');

@Component({})
export default class BasketCardSpec extends Vue {

  @cart.State
  public cartArr
  @cart.Mutation
  public delById!: (delById) => void
  @cart.Mutation
  public setCartArr!: (newCartItem: CartItem) => void

  @modal.Mutation
  public setModal!: (newState: boolean) => void
  @modal.Mutation
  public setIsInBasketAllItemsRemove!: (newState: boolean) => void

  @categoryModule.State
  public prefix!: string

  private filteredRecItems = recItems;
  private showRecText: boolean = true;

  // ----- Filter recommended items -----
  created() {

    const arraySparse = this.cartArr;
    let numCallbackRuns = 0;
    let countItemsType0 = 0;
    let countItemsType1 = 0;

    arraySparse.forEach((element) => {
      if (element.type === 0) {
        countItemsType0++
      }
      if (element.type === 1) {
        countItemsType1++
      }
      numCallbackRuns++
    })

    if(countItemsType0 === 0) {
      this.filteredRecItems = recItems.filter(item => item.type !== 1);
    }
    if(countItemsType1 === 0) {
      this.filteredRecItems = recItems.filter(item => item.type !== 0);
    }
    if(countItemsType0 !== 0 && countItemsType1 !== 0) {
      this.filteredRecItems = [];
      this.showRecText = false;
    }
  }

  formattedCur(cost: number) { 

    let nFormat;
    let convertCost;
    let postFix;

    // TODO: Need currency Api
    switch (this.prefix) {
      case "RUB":
        nFormat = "ru-RU";
        convertCost = cost;
        postFix = "₽";
         // postFix = "";
        break;
      case "UAH":
        nFormat = "ru-RU";
        convertCost = cost/2.66;
        // convertCost = cost/2.7;
        postFix = "₴";
        break;
      case "BYN":
        nFormat = "ru-RU";
        convertCost = cost/30;
        postFix = "Br";
        break;
      case "KZT":
        nFormat = "ru-RU";
        convertCost = cost/0.175;
        // convertCost = cost/0.17;
        postFix = "₸";
        break;
      case "USD":
        nFormat = "en-EN";
        convertCost = cost/73.64;
        // convertCost = cost/73.6;
        postFix = "$";
        break;
      case "EUR":
        nFormat = "de-DE";
        convertCost = cost/87.18;
        // convertCost = cost/87.2;
        postFix = "€";
        break;
      default:
        nFormat = "ru-RU";
        convertCost = cost;
        postFix = "₽";
    }

    const formattedCost = new Intl.NumberFormat(nFormat, 
      {
        style:'decimal',
        currency: this.prefix,minimumFractionDigits: 2,
        maximumFractionDigits: 2
      }).format(convertCost) + " " + postFix;

    return formattedCost;
  }
  addClick(basketItem) {

    this.filteredRecItems = this.filteredRecItems.filter(item => item.id !== basketItem.id);

    // hack
    basketItem.qtty = 1;

    this.setCartArr(basketItem);

    if (this.filteredRecItems.length === 0) {
      this.showRecText = false;
    }
  }
}
