
import { Vue, Prop, Component } from 'vue-property-decorator'

import { namespace } from 'vuex-class';
const categoryModule = namespace('CategoryModule');
const modal = namespace('Modal');
const cart = namespace('Cart');

@Component({})
export default class BasketCardView extends Vue {

  @cart.State
  public cartArr
  @cart.Mutation
  public delById!: (delById) => void
  @cart.Mutation
  public addCountItem!: (id) => void
  @cart.Mutation
  public subCountItem!: (id) => void
  @cart.Mutation
  public getCountItem!: (id) => void

  @modal.Mutation
  public setModal!: (newState: boolean) => void
  @modal.Mutation
  public setIsInBasketAllItemsRemove!: (newState: boolean) => void

  @categoryModule.State
  public prefix!: string

  // private stateSpecType: number = true;

  formattedCur(cost: number) { 

    let nFormat;
    let convertCost;
    let postFix;

    // TODO: Need currency Api
    switch (this.prefix) {
      case "RUB":
        nFormat = "ru-RU";
        convertCost = cost;
        postFix = "₽";
         // postFix = "";
        break;
      case "UAH":
        nFormat = "ru-RU";
        convertCost = cost/2.66;
        // convertCost = cost/2.7;
        postFix = "₴";
        break;
      case "BYN":
        nFormat = "ru-RU";
        convertCost = cost/30;
        postFix = "Br";
        break;
      case "KZT":
        nFormat = "ru-RU";
        convertCost = cost/0.175;
        // convertCost = cost/0.17;
        postFix = "₸";
        break;
      case "USD":
        nFormat = "en-EN";
        convertCost = cost/73.64;
        // convertCost = cost/73.6;
        postFix = "$";
        break;
      case "EUR":
        nFormat = "de-DE";
        convertCost = cost/87.18;
        // convertCost = cost/87.2;
        postFix = "€";
        break;
      default:
        nFormat = "ru-RU";
        convertCost = cost;
        postFix = "₽";
    }

    const formattedCost = new Intl.NumberFormat(nFormat, 
      {
        style:'decimal',
        currency: this.prefix,minimumFractionDigits: 2,
        maximumFractionDigits: 2
      }).format(convertCost) + " " + postFix;

    return formattedCost;
  }
  removeClick(par) {
    this.delById(par);

    if (this.cartArr.length === 0) {
      this.setModal(false);
      this.setIsInBasketAllItemsRemove(true);
    }
  }
  addCountItemClick(arg) {
    this.addCountItem(arg);

    // Vue.forceUpdate();
    this.$forceUpdate();
  }
  subCountItemClick(arg) {
    this.subCountItem(arg);

    this.$forceUpdate(); 
  }
  testRender(arg) {

    // const itemValue = this.getCountItem(arg.id)

    // const countItem = this.cartArr.find(item => item.id === arg.id);

    const countItem = this.cartArr.find(item => item.id === 520597);

    return countItem.qtty;
  }
}
