
import { Vue, Prop, Component } from 'vue-property-decorator';
import { CategorieItem } from "../types";
import { namespace } from 'vuex-class';
const categoryModule = namespace('CategoryModule')

@Component
export default class CardView extends Vue {
  @Prop() categorieItem?: CategorieItem;
  private isActive: boolean = false;
  private curActive: number;
  // Create variable for vuex, current state of categorie button;

  @categoryModule.State
  public categoryName!: string

  // @categoryModule.Getter
  // public nameUpperCase!: string

  @categoryModule.Mutation
  public setName!: (newName: string) => void
  @categoryModule.Mutation
  public setId!: (newId: number) => void

  @categoryModule.Action
  public updateName!: (newName: string) => void

  methods() {
    const toggle2 =  function(event) {
      this.isActive = !this.isActive;
    }
  }
  toggle() {
    this.isActive = !this.isActive;
    //this.setName(this.categorieItem.name);

    // const idConverted = Number(this.categorieItem.id);
    this.setId(this.categorieItem.id);
  }
  toggleActive(event) {

    const element = event.currentTarget;
    const itemHasClass = element.classList.contains('active');

    if (!itemHasClass) {

      // Clear all active state if this is not this element
      const elDoIsActive = function (element1) {
        if (element1.isActive !== undefined) {
          element1.isActive = false;
        }
      }

      this.$parent.$children.forEach(element1 => element1.$el !== element ? elDoIsActive(element1) : null );

      this.toggle();
    }
  }

  mounted() {
    // Category 'All'
    if(this.categorieItem.id === 1) {
      //this.isActive = true;
      this.toggle();
    } else {
      this.isActive = false;
    }
  }
  // computed() {
  //   const test = function(event) {
  //     return 'Пользователь: ' + this.name.toUpperCase();
  //   }
  // }

}
